<template>
  <div class="cart container">
    <header>
      <i class="iconfont icon-fanhui" @click="backNextPage"></i>
      <span>购物车</span>
      <i class="iconfont icon-bianji" @click="isNavaBar"></i>
    </header>

    <section>
      <div v-show="hasShop">
        <div class="sp">
          <van-checkbox
            v-model="checked"
            checked-color="#ee0a24"
            @click="checkAllFn"
          ></van-checkbox
          ><span>商品</span>
        </div>
        <div
          class="shopList"
          v-for="(item, index) in shopList"
          :key="index"
          style="margin-top: 0.3rem"
        >
          <div class="shopList-left">
            <van-checkbox
              :value="item.goods_check == 1 ? true : false"
              checked-color="#ee0a24"
              @click="changeOneList(item.id, index)"
            ></van-checkbox>
          </div>
          <div class="shopList-center">
            <img :src="item.goods_imgUrl" alt="" />
          </div>
          <div class="shopList-right">
            <span class="span"
              ><b>{{ item.goods_recommed }}</b>
              <i
                class="iconfont icon-changyonggoupiaorenshanchu"
                @click="deleteThisShop(item.id, index)"
              ></i
            ></span>
            <span class="span"
              ><b><span>名称:</span> {{ item.goods_name }}</b></span
            >
            <span class="span"
              ><b style="color: red"><span>￥</span>{{ item.goods_price }}</b>
            </span>
            <span class="span"
              ><b><span>数量:</span> {{ item.goods_num }}</b>
              <van-stepper
                v-model="item.goods_num"
                :disable-input="true"
                @change="changeNumber(item.id, item.goods_num)"
              />
            </span>
          </div>
        </div>
      </div>
      <div v-show="!hasShop" class="hasShop">
        <img src="@/../public/images/暂无数据.png" alt="" />
        <h3><i class="iconfont icon-tixing1"></i> 您的购物车中暂无商品</h3>
      </div>
    </section>
    <footer v-show="hasShop && !isNavStatus">
      <div class="radio">
        <van-checkbox
          v-model="checked"
          checked-color="#ee0a24"
          @click="checkAllFn"
        ></van-checkbox>
      </div>
      <div class="shopTotal">
        <span
          >共有<b>{{ allNumber }}</b
          >件商品</span
        >
        <span
          >总共：<b>￥{{ allMoney }}+0茶币</b></span
        >
      </div>
      <div class="toSettle" @click="gotoOrderPage">去结算</div>
    </footer>
    <div
      v-show="isNavStatus && hasShop"
      class="deleteShop"
      @click="allDeleteShop"
    >
      删除
    </div>
  </div>
</template>

<script>
import {
  getShopList,
  deleteShopList,
  deleteAllShopList,
  updatedShopNum,
} from "@/api/shopCart.js";
import { addOrder } from "@/api/order.js";
import { mapMutations } from "vuex";
import { Toast, Dialog } from "vant";

export default {
  name: "Cart-vue",
  data() {
    return {
      shopList: [], //存储返回的购物车数据列表
      hasShop: true, //判断用户购物车中是否有数据
      checked: true,
      selectList: [], //选中的数据
      isNavStatus: false, //判断当前是否为编辑状态
      list: [], //用户选中的商品列表
    };
  },
  created() {
    this.getData();
  },
  activated() {
    this.getData();
  },
  methods: {
    ...mapMutations(["LIST_DATA"]),
    async getData() {
      let res = await getShopList();
      if (res.data.code == 200) {
        if (res.data.data.r.length > 0) {
          this.hasShop = true;
          this.shopList = res.data.data.r;
          this.LIST_DATA(res.data.data.r);
          this.selectList = this.shopList.map((item) => {
            return item.id;
          });
        } else {
          this.hasShop = false;
        }
      }
    },
    backNextPage() {
      this.isNavStatus = false;
      this.$router.push("/home");
    },
    // // 判断是否全选
    checkAllFn() {
      if (this.selectList.length == this.shopList.length) {
        this.checked = false;
        this.shopList.forEach((item) => {
          item.goods_check = 2;
        });
        this.selectList = [];
      } else {
        this.checked = true;
        this.shopList.forEach((item) => {
          item.goods_check = 1;
          if (this.selectList.indexOf(item.id) == -1) {
            this.selectList.push(item.id);
          }
        });
      }
    },
    //单选按钮
    changeOneList(id, index) {
      if (this.shopList[index].goods_check == 1) {
        let i = this.selectList.indexOf(id);
        this.selectList.splice(i, 1);
        this.shopList[index].goods_check++;
        this.checked = false;
      } else {
        this.shopList[index].goods_check--;
        this.selectList.push(id);
        if (this.selectList.length == this.shopList.length) {
          this.checked = true;
        }
      }
    },
    //点击编辑图标回调
    isNavaBar() {
      if (this.hasShop) {
        this.isNavStatus = !this.isNavStatus;
      }
    },
    //商品单个删除事件
    deleteThisShop(id) {
      Dialog.confirm({
        title: "删除",
        message: "您确定要删除吗？",
      }).then(async () => {
        let res = await deleteShopList(id);
        if (res.data.code == 200) {
          this.getData();
          Toast("删除成功！");
        }
      });
    },
    //多个商品删除事件
    async allDeleteShop() {
      if (this.selectList.length == 0) {
        Toast("请选择商品");
      } else {
        Dialog.confirm({
          title: "删除",
          message: "您确定要删除吗？",
        }).then(async () => {
          let res = await deleteAllShopList(this.selectList);
          if (res.data.code == 200) {
            this.getData();
            this.selectList = [];
          }
        });
      }
    },
    //修改购物车商品数量的回调
    async changeNumber(id, value) {
      let res = await updatedShopNum(id, value);
      if (res.data.code == 200) {
        console.log("修改成功");
      }
    },
    //点击去结算按钮的回调
    async gotoOrderPage() {
      if (this.selectList.length > 0) {
        //生成一个支付订单
        for (let i of this.shopList) {
          if (this.selectList.indexOf(i.id) != -1) {
            this.list.push(i);
          }
        }
        let res = await addOrder(this.list);
        if (res.data.code == 200) {
          this.$store.state.cart.productToken = res.data.data.producT; //将返回的用户订单号存到store中
          //将用户订单的token进行本地化存储，到order页面中读取后再到数据库中对
          //token订单进行查询并返回后渲染到页面中去。可以解决页面刷新后数据丢失的问题。
          this.$router.push({
            name: "Order",
            params: {
              num: this.allNumber,
              price: this.allMoney,
              selectList: this.selectList,
            },
          });
        }
      } else {
        Toast("您还未选择商品");
      }
    },
  },
  computed: {
    //计算所有的money
    allMoney() {
      let sum = 0;
      this.shopList.forEach((v) => {
        if (v.goods_check == 1) {
          sum = sum + v.goods_price * v.goods_num;
        }
      });
      return sum.toFixed(2);
    },
    //计算所有的number
    allNumber() {
      let num = 0;
      this.shopList.forEach((v) => {
        if (v.goods_check == 1) {
          num = num * 1 + v.goods_num * 1;
        }
      });
      return num;
    },
  },
};
</script>

<style scoped lang="less">
.cart {
  background-color: #fafafa;
}
header {
  box-sizing: border-box;
  display: flex;
  width: 100%;
  height: 1.74rem;
  background-color: #b0352f;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  padding: 0 0.5rem;
  i {
    font-size: 0.7rem;
  }
  span {
    font-size: 0.5rem;
  }
}
section {
  width: 100%;
  flex: 1;
  .hasShop {
    width: 100%;
    height: 8rem;
    img {
      margin-top: 0.5rem;
      margin-left: 10%;
      width: 80%;
      height: 6rem;
    }
    h3 {
      margin: 0.5rem auto;
      width: 100%;
      height: 1.5rem;
      font-size: 0.6rem;
      font-weight: 400;
      text-align: center;
      i {
        font-size: 0.7rem;
        font-weight: 500;
        color: #b0352f;
      }
    }
  }
  .sp {
    box-sizing: border-box;
    width: 100%;
    height: 1.5rem;
    line-height: 1.5rem;
    padding-left: 0.5rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    span {
      margin-left: 0.4rem;
    }
  }
  .shopList {
    margin-top: 0.3rem;
    border-top: 1px solid #ccc;
    border-bottom: 1px solid #ccc;
    width: 100%;
    padding-left: 0.5rem;
    padding-right: 0.3rem;
    height: 3rem;
    line-height: 3rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    .shopList-center {
      width: 2.5rem;
      height: 2.5rem;
      margin-left: 0.3rem;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .shopList-right {
      height: 2.8rem;
      width: 40%;
      margin-left: 0.4rem;
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: flex-start;
      .span {
        width: 80%;
        font-size: 0.4rem;
        height: 0.6rem;
        line-height: 0.6rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        i {
          font-size: 0.5rem;
          font-weight: 500;
        }
        b {
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }
    }
  }
}
.deleteShop {
  box-sizing: border-box;
  margin-left: 15%;
  height: 1.5rem;
  width: 70%;
  border-top: 1px solid #ccc;
  font-size: 0.7rem;
  text-align: center;
  line-height: 1.5rem;
  background-color: #b0352f;
  color: #fff;
}
footer {
  box-sizing: border-box;
  width: 100%;
  z-index: 999;
  height: 1.74rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 0.5rem;
  border-top: 1px solid #ccc;
  .radio {
    input {
      width: 0.5rem;
      height: 0.5rem;
      background-color: #b0352f;
    }
  }
  .shopTotal {
    padding-left: 0.4rem;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    span {
      font-size: 0.4rem;
      b {
        color: red;
        font-weight: 500;
      }
    }
  }
  .toSettle {
    width: 30%;
    height: 1.74rem;
    line-height: 1.74rem;
    background-color: #b0352f;
    color: #fff;
    text-align: center;
    font-size: 0.5rem;
    font-weight: 500;
  }
}
</style>
