import request from "./request";
//添加购物车接口
export const addShopCart = (id) => {
  return request({
    url: "/api/shop/addCart",
    method: "post",
    data: {
      goodsId: id,
    },
  });
};
//获取购物车数据接口
export const getShopList = () => {
  return request({
    url: "/api/shop/getShopList",
    method: "get",
  });
};
//删除购物车数据接口（单个删除）
export const deleteShopList = (id) => {
  return request({
    url: "/api/shop/deleteCart",
    method: "post",
    data: {
      id,
    },
  });
};
//删除购物车数据接口（多个删除）
export const deleteAllShopList = (idArr) => {
  return request({
    url: "/api/shop/deleteAllCart",
    method: "post",
    data: {
      idArr,
    },
  });
};
//修改购物车商品中的数量
export const updatedShopNum = (id, num) => {
  return request({
    url: "/api/shop/updateShopNum",
    method: "post",
    data: {
      id,
      num,
    },
  });
};
